
.salem{
    margin: 100px 20px 20px 20px;
}
.album{
    display: flex;
    justify-content: 1;
    gap: 5%;
    flex-wrap: wrap;
    margin: 20px 0;
}
.album div{
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}
.album div img{
    width: 100%;
} 

@media (max-width: 800px){
    .album{
        flex-wrap: wrap;
        flex-direction: column;
    }
    .album div img{
        width: 80%;
        margin: 20px 0;
    }
}