.contact-box{
margin: 100px 20px 20px 20px;
display: flex;
flex-direction: column;
align-items: center;
}
.contact-box h1{
    padding-top: 20px;
}
.contact{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 30px;
}
form{
    background-color: rgb(150, 140, 140);
    padding: 40px;
    border-radius: 10px;
}
input,textarea{
    border: none;
    outline: 0;
    padding: 5px 10px;
    margin: 2px 0 7px 0;
    border-radius: 4px;
}
.submit{
    width: 100px;
    padding: 5px 0;
    margin-top: 10px;
    border-radius: 5px;
    cursor: pointer;
    border: none;
    background-color: #3882F6;
    color: white;
    font-weight: bold;
}