.introduction{
  display: flex;
  justify-content: center;
  margin: 100px 20px 20px 20px;
  background-color: rgb(233, 238, 241);
  padding: 20px;
  border-radius: 8px;
}

.more{
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 0 20px 20px 20px;
}
.card{
  flex-basis: 350px;
  border: 3px solid rgb(190, 185, 185);
  border-radius: 10px;
  padding-bottom: 10px;
  flex: 1;
}
.card h3,p{
 margin-left: 20px;
}
.profile{
  width: 100%;
  height: 300px;
}

@media (max-width: 800px){
  .more{
    display: flex;
    flex-direction: column;
  }
}

