*{
    list-style: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    box-sizing: border-box;
}
header{
    height: 100px;
    width: 100%;
    background-color: #1F2937;
    position: fixed;
    top: 0;
    z-index: 100;
}
header nav{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.logo{
    border-radius: 50%;
}
.img{
    padding: 10px 40px 10px 20px;
}
.color{
    color: #E5E7EB;
    font-size: 13px;
    color: #E5E7EB;
}

header nav ul{
  display: flex;
  flex: 1;
 
}
header nav ul li{
    text-transform: uppercase;
    flex: 1;
    display: block;
    height: 100px;
    line-height: 100px;
    
}
header nav ul ul{
    display: none;
    background-color: #1F2937;
    animation: semi 1.8s alternate;
    border-radius: 0 0 5px 5px;
    padding-bottom: 7px;
}
header nav ul li:hover ul{
 display: block;
 position: absolute;
 top: 100px;
}
header ul ul li{
    padding: 5px 10px;
    line-height: 40px;
    height: 40px;
}
.bars{
    width: 35px;
    height: 26.5px;
    margin-right: 20px;
    cursor: pointer;
    display: none;
}
.bar{
    width: 100%;
    height: 4px;
    background-color: white;
    margin: 3.5px 0;
}

.active{
    background-color: orange;
    padding: 4px 7px;
    border-radius: 3px;
    transition: all .2 ease-in-out;
}
/*this is animation for header semi ul element*/
@keyframes semi{
    from{top: 140px}
    to{top: 100px}
}

/*the styles below this line is for taplet divices atleast of 816px */
@media (max-width: 816px){
    .img{
        margin-right: auto;
    }
    .bars{
        display: block;
    }
    nav ul{
        display: flex;
        flex-direction: column;
        position: fixed;
        align-items: flex-start;
        background-color: #3882F6;
        top: 100px;
        width: 100%;
        gap: 10px;
        padding: 20px 40px;
        transition: all 1.1s;
        right: -100%;
        border-radius: 0 0 0 10px;
        z-index: 100;
    }
    header nav ul  li:hover ul{
        display: block;
        background-color: #1F2937;
        line-height: 20px;
        background-color: #3882F6;
        position: relative;
        right: 20%;
       top: 0;
        }

        
header nav ul li{
    flex-grow: 0;
    height: 20px;
    line-height: 20px;
}
header ul ul li{
    padding: 0;
}
@keyframes semi{
    from{top: 0}
    to{top: 0}
}
.active{
    background-color:#3882F6;
    color: orange;
}
}

/*the styles below this line is for mobile divice atleast the one of 375px and below it*/
@media (max-width: 375px){
    .img{
        margin-right: auto;
    }
    .bars{
        display: block;
    }
    nav ul{
        display: flex;
        flex-direction: column;
        position: fixed;
        top: 100px;
        width: 100%;
      align-items: flex-end;
      background-color: #3882F6;
      align-items: flex-start;
      padding: 20px 10px;
      transition: all 1.1s;
      right: -100%;
      line-height: 30px;
    }
    
}