.blog{
    margin: 100px 20px 20px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.blog h1{
    padding: 20px;
}
.service{
    width: 100%;
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 30px;
}
.work{
   width: 440px;
   background-color: rgb(153, 228, 153);
   padding: 6px 10px;
   border-radius: 8px;
   margin: 10px 0;
   flex-grow: 1;
   line-height: 32px;
}
.action{
    padding-left: 15px;
}