
.horeb{
    margin: 100px 20px 20px 20px;
}

.galley{
    display: flex;
    justify-content: 1;
    gap: 5%;
    flex-wrap: wrap;
    margin: 20px 0;
}
.galley div{
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}
.galley div img{
    width: 100%;
} 
.text{
    padding: 10px 0;
}
@media (max-width: 800px){
    .galley{
        flex-wrap: wrap;
        flex-direction: column;
    }
    .galley div img{
        width: 80%;
        margin: 20px 0;
    }
}