.preaches{
    margin: 100px 20px 20px 20px;
}
.preaches h1{
    text-align: center;
}
.preaches>div *{
padding: 10px 0;
font-size: 17px;
}
.part-zero h3{
 display: flex;
 justify-content: center;
 animation: jumping 1s infinite;
 
}

@keyframes jumping{
    100%{
        transform: translateY(-10px)
    }
    90%{
        transform: translateY(2px)
    }
}
.part-zero p{
    display: flex;
 justify-content: center;
 background-color: aqua;
 border-radius: 6px;
 animation: changing 3s infinite;
 color: white;
}

@keyframes changing{
100%{
    background-color: blue;
}
80%{
    background-color: darkblue;
}
60%{
    background-color: darkgreen;
}
}