.Home{
  background-image: url(./IMG/cross.webp);
  width: 100%;
  height: 100%;
  background-size: cover;
}
.heading{
  margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    position: relative;
}
.header{
 font-size: 20px;
 text-align: center;
 padding: 4px 10px;
 border-radius: 7px;
}
.welcome{
    animation: mine 3s reverse;  
    background-color: rgb(57, 211, 57);
    width: 200px;
    margin: 10px 0 8px 0;
    height: auto;
    flex-shrink: 1;
}
.name{
    animation: title 3s reverse ;
    background-color: darkcyan;
    width: 180px;
    height: auto;
} 


@keyframes mine{
   100%{
    transform: translateX(260%)
    
   }
  }
@keyframes title{
    100%{
      transform: translateX(-260%)
      }
}

@media (max-width: 375px){
    .welcome,.name{
        width: 150px;
        font-size: 15px;
        padding: 4px 5px;
        border-radius: 3px;
    }
}