
footer{
    background-color: #1F2937;
    height: auto;
    width: 100%;
    padding: 10px;
    color: white;
   margin-top: auto;
   font-family: sans-serif;
}
.footer ul{
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 10px;
}
.footer ul li{
 font-size: 20px;
}
.Color{
    color: whitesmoke;
    font-family: sans-serif;
}
.copy{
    display: flex;
    justify-content: center;
    font-size: 14px;
}
@media (max-width: 375px){
    .footer ul{
        flex-direction: column;
        align-items: center;
        gap: 5px;
        margin-bottom: 10px;
    }
}